import React from 'react';

const FormField = ({
  label,
  children,
  error,
  help,
  required = false,
  className = '',
  ...props
}) => {
  const fieldId = props.id || `field-${Math.random().toString(36).substr(2, 9)}`;

  return (
    <div className={`input-group ${className}`} {...props}>
      {label && (
        <label htmlFor={fieldId} className="label-base">
          {label}
          {required && <span className="text-error ml-1">*</span>}
        </label>
      )}
      {React.Children.map(children, child =>
        React.isValidElement(child) 
          ? React.cloneElement(child, { id: fieldId, ...child.props })
          : child
      )}
      {error && (
        <div className="text-error text-sm mt-1">
          {error}
        </div>
      )}
      {help && (
        <div className="form-help">
          {help}
        </div>
      )}
    </div>
  );
};

const Input = React.forwardRef(({
  type = 'text',
  variant = 'default',
  size = 'default',
  error,
  className = '',
  ...props
}, ref) => {
  const baseClass = 'form-control';
  const variantClass = type === 'textarea' ? 'textarea' : '';
  const errorClass = error ? 'error' : '';
  const sizeClass = size === 'small' ? 'form-control--sm' : 
                   size === 'large' ? 'form-control--lg' : '';

  const classes = [baseClass, variantClass, errorClass, sizeClass, className]
    .filter(Boolean)
    .join(' ');

  if (type === 'textarea') {
    return (
      <textarea
        ref={ref}
        className={classes}
        {...props}
      />
    );
  }

  return (
    <input
      ref={ref}
      type={type}
      className={classes}
      {...props}
    />
  );
});

Input.displayName = 'Input';

FormField.Input = Input;

export default FormField;