import React from 'react';

const Card = ({
  children,
  variant = 'default',
  padding = 'md',
  hover = false,
  className = '',
  onClick,
  ...props
}) => {
  const getVariantClass = () => {
    switch (variant) {
      case 'function':
        return 'function-card';
      case 'base':
        return 'card-base';
      case 'usage':
        return 'function-card usage-card';
      default:
        return 'card-base';
    }
  };

  const getPaddingClass = () => {
    switch (padding) {
      case 'none':
        return '';
      case 'sm':
        return 'card-padding-sm';
      case 'md':
        return 'card-padding';
      case 'lg':
        return 'p-lg';
      default:
        return 'card-padding';
    }
  };

  const baseClass = getVariantClass();
  const paddingClass = getPaddingClass();
  const hoverClass = hover ? 'card-hover' : '';
  const clickableClass = onClick ? 'cursor-pointer' : '';
  
  const classes = [
    baseClass,
    paddingClass,
    hoverClass,
    clickableClass,
    className
  ].filter(Boolean).join(' ');

  return (
    <div 
      className={classes}
      onClick={onClick}
      {...props}
    >
      {children}
    </div>
  );
};

export default Card;